import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {UtilitybillpayProvider} from "../../providers/utilitybillpay/utilitybillpay";
import {AlertProvider} from "../../providers/alert/alert";

/**
 * Generated class for the Utilitybillpay03Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-utilitybillpay03',
  templateUrl: 'utilitybillpay03.html',
})
export class Utilitybillpay03Page {
  menuTitle;
  debitAccDetails;
  companyDetails;
  selectedCategory;
  categoryDesc;
  billingAccount;
  billNo;
  amount;
  charge;
  totAmount;
  utilityId;
  compGenerateBill;
  preview;
  isNewReg;
  companyBillData;
  otp;
  utilityBillPayError03;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public tokenService: TokenServiceProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public billPayService: UtilitybillpayProvider,
              public alertProvider: AlertProvider) {

    this.utilityBillPayError03 = "";
    this.menuTitle = this.navParams.get("menuTitle");

    this.billingAccount = this.navParams.get("billingAccount");

    let com = this.navParams.get("companyDetails");
    this.companyDetails = JSON.parse(com);

    this.selectedCategory = this.navParams.get("selectedCategory");
    this.categoryDesc = this.navParams.get("categoryDesc");

    this.debitAccDetails = JSON.parse(this.navParams.get("debitAccDetails"));
    this.isNewReg= this.navParams.get("isNewReg");
    this.billNo = this.navParams.get("billNo");
    this.amount = this.navParams.get("amount");
    this.charge = this.navParams.get("charge");
    this.totAmount = this.navParams.get("totAmount");
    this.utilityId = this.navParams.get("utilityId");
    this.compGenerateBill=this.navParams.get("compGenerateBill");
    this.preview = JSON.parse(this.navParams.get("preview"));
    this.companyBillData=this.navParams.get("companyBillData");

    // console.log(this.companyBillData);

  }

  ConfirmPayUtilityBillAction = function (oneTimePass) {
    this.utilityBillPayError03 = "";
    this.otp = oneTimePass;
    let flag = true;

    let validateOtpExp = new RegExp("[0-9]{6}$");
    if (!validateOtpExp.test(oneTimePass)) {
      this.utilityBillPayError03 = "Invalid OTP format";
      flag = false;
      this.otp = "";
      return;
    }

    this.otp = oneTimePass;

    if (flag)
    {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encEnqueueID = this.cryptoUtil.getEncryptVal(this.utilityId);
      let encTPIN = this.cryptoUtil.getEncryptVal(this.tpin);
      let encOTP = this.cryptoUtil.getEncryptVal(this.otp);
      let encIsNewReg = this.cryptoUtil.getEncryptVal(this.isNewReg);
      let encCompanyBillData = this.cryptoUtil.getEncryptVal(this.companyBillData);

      this.billPayService.PayUtilityBill(unMaskToken, appData.userId, appData.appId, encEnqueueID, encTPIN, encOTP, encIsNewReg, encCompanyBillData)
        .then(data => {
          this.PayUtilityBillComplete(data);
        });
    }
  };

  PayUtilityBillComplete = function (data) {
    // console.log(data);
    if (data.statusCode == 1) {
      this.navCtrl.push('Utilitybillpay04Page', {
        'menuTitle': this.menuTitle,
        'selectedCategory': this.selectedCategory,
        'categoryDesc': this.categoryDesc,
        'companyDetails' : JSON.stringify(this.companyDetails),
        'billingAccount' : this.billingAccount,
        'totAmount' : this.totAmount,
        'utilityId' : this.utilityId,
      });
    }

    else if (data.statusCode == 0) {
      this.utilityBillPayError03 = data.note;
      this.otp="";
      return;
    }

    else {
      this.utilityBillPayError03 = "System Error Contact with Administrator.";
      this.otp="";
      return;
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad Utilitybillpay03Page');
  }

}



